<template>
<div id="addproductneed">
<van-nav-bar
  title="添加产品需求"
  left-arrow
  class="addproductneed-nav"
   @click-left="leftClick"
/>
	<div class="addproductneed-content">
		<van-cell-group inset >
			<h4>产品信息</h4>
			<!-- 输入任意文本 -->
			<van-field
				v-model="selectedProduct.title"
				is-link
				readonly
				label="产品"
				placeholder="产品选择"
				@click="showPicker = true"
			/>
			<van-popup v-model="showPicker" round position="bottom">

				<van-picker
					title="请选择产品"
					show-toolbar
					:columns="productList"
					value-key="text"
					@confirm="onConfirm"
					@cancel="showPicker = false"
				/>
			</van-popup>
			<van-field v-model="selectedProduct.costs" label="产品造价" readonly placeholder=""/>
			<van-field v-model="productInfo.specs" label="产品规格" readonly placeholder=""/>
			<van-field v-model="productInfo.exclusiveprice" label="产品单价" placeholder="请输入价格(单位/元)"/>
			<van-field v-model="selectedProduct.manufacturer" readonly label="生成厂商"/>
		</van-cell-group>
		<div class="addProductneed-tips">
			<ul class="tip-ui">
				<li>1. 点击选中产品</li>
				<li>2. 输入客户售价</li>
				<li>3. 确定提交信息</li>
			</ul>
		</div>
	</div>
	<div class="bottom-box">
		<i class="sureBtn" @click="sureClick">确定</i>
	</div>
</div>
	
</template>

<script>
import { NavBar,Field, CellGroup,Picker,ActionSheet,Popup} from 'vant'

import {addProduct,getAllProduct,addProductNeed} from 'network/product'


export default {
  name: 'AddProductNeed',
	components:{
		[NavBar.name]:NavBar,
		[Field.name]:Field,
		[CellGroup.name]:CellGroup,
		[Picker.name]:Picker,
		[ActionSheet.name]:ActionSheet,
		[Popup.name]:Popup,
	},
	data(){
		return{
			productInfo:{
				productid:0,							//选中的商品的id
				customerid:0,							//客户的id
				exclusiveprice:'',						//定制的价格
				specs:'',								//规格
			},
			productList:[],								//产品列表
			show: false,
			showPicker:false,
			selectedProduct:'',
		}
	},
	methods:{	
		//返回上级
		leftClick(){
			this.$router.go(-1)
		},
		//保存按钮
		sendClick(){
			addProduct(this.productInfo).then(res=>{
				console.log(res)
			})
			//如果成功就清除所有表单按钮并且跳转
		},
		onConfirm(value){
			this.showPicker=false							//隐藏底部弹窗
			this.selectedProduct=value						//选中的对象
			this.productInfo.productid=value.id				//选中的id
			this.productInfo.specs=value.specs				//选中的规格
		},
		//选取完成
		sureClick(){
			//console.log(this.productInfo.exclusiveprice+"和"+this.selectedProduct.price)
			//验证是否已经选中才能点确定按钮
			//必须选中
			if(this.productInfo.productid==''){
				//弹窗
				this.$toast('请选泽产品')
				return
			}
			//定制价格不能为空
			if(this.productInfo.exclusiveprice==''){
				//弹窗
				this.$toast('请输入定制售价')
				return
			}
			//价格不能太低
			if(this.selectedProduct.costs>parseFloat(this.productInfo.exclusiveprice)){
				//弹窗
				this.$toast('售价不能比造价低')
				return
			}
			//需要做一做判断后台是否已经有了该条产品信息如果没有再通过-*该后台做

			//1.发送数据到后台
			//是否新增数据成功
			addProductNeed(this.productInfo).then(res=>{
				this.$toast(res)
			})
			//2.清空选中数值
			//localStorage.removeItem('customerdetailid')
			this.selectedProduct=''
			this.productInfo.exclusiveprice=''
			this.productInfo.specs=''
			this.productInfo.productid=0
			
			//3.返回上级页面
			this.$router.push({name:'customerdetail'}).catch(res=>{res})
		}
	},
	computed:{

	},
	//获取所有产品
	mounted(){
		getAllProduct().then(res=>{
			this.productList.push(...res)
		})
	},
	activated(){
		this.selectedProduct=''
		this.productInfo.exclusiveprice=''
		this.productInfo.specs=''
		this.productInfo.productid=0
		
		//进来先获取上级页面点击的客户id
		this.productInfo.customerid=localStorage.getItem('customerdetailid')?localStorage.getItem('customerdetailid'):0
	}
}
</script>

